	module.exports = {"data":{"markdownRemark":{"html":"<h2>Orthogonality</h2>\n<p>Orthogonality is a software design principle for writing components in a way that changing one component doesn’t affect other components. It is the combination of two other principles, namely strong cohesion and loose coupling.</p>\n<h3>Strong Cohesion</h3>\n<p>Inside a software component, code should be strongly connected. This is an indication that the code is correctly divided. If a component had two or more relatively disconnected parts, that may indicate that those parts should be in a different component, or on it’s own.</p>\n<h3>Loose Coupling</h3>\n<p>Between software components, there should be few connections. If two components are strongly coupled, it may indicate that they need to be one component, or that they need to be differently divided into more components.</p>\n<h4>More Information:</h4>\n<ul>\n<li><a href='http://www.jasoncoffin.com/cohesion-and-coupling-principles-of-orthogonal-object-oriented-programming/' target='_blank' rel='nofollow'>Principles of Orthogonal Object Oriented Programming</a></li>\n<li><a href='https://en.wikipedia.org/wiki/GRASP_(object-oriented_design)' target='_blank' rel='nofollow'>GRASP - object-oriented design principles</a></li>\n</ul>","fields":{"slug":"/software-engineering/orthogonality/"},"frontmatter":{"title":"Orthogonality"}}},"pathContext":{"slug":"/software-engineering/orthogonality/"}}

